
















export default {
	data() {
		return {
			news: []
		}
	},
	onLoad() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.news = res.data
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfo(e) {
			uni.navigateTo({
				url: '../info/info?id=' + e.currentTarget.dataset.newsid
			})
		}
	}
}
